import Vue from "vue";
import VueRouter from "vue-router";

//layouts for the application
import List from "@/layouts/list/Index.vue";
import Item from "@/layouts/item/Index.vue";

// views for the application
import CardList1 from "@/views/list/CardList1.vue";
import CardList2 from "@/views/list/CardList2.vue";
import CardList3 from "@/views/list/CardList3.vue";

import CardItem1 from "@/views/item/CardItem1.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "List",
    component: List,
    children: [
      {
        path: "card-list-1",
        name: "CardList1",
        component: CardList1,
      },
      {
        path: "card-list-2",
        name: "CardList2",
        component: CardList2,
      },
      {
        path: "card-list-3",
        name: "CardList3",
        component: CardList3,
      },
    ],
  },
  {
    path: "/item",
    name: "Item",
    component: Item,
    children: [
      {
        path: "card-item-1",
        name: "CardItem1",
        component: CardItem1,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
